import { createRouter, createWebHistory } from 'vue-router'

const routes = [{
        path: '/',
        name: 'Home',
        component: () =>
            import ('../views/Home.vue')
    },
    {
        path: '/home',
        redirect: '/'
    },
    {
        path: '/contact',
        name: 'Contact',
        component: () =>
            import ('../views/Contact.vue')
    },
    {
        path: '/about',
        name: 'About',
        component: () =>
            import ('../views/About.vue')
    },
    {
        path: '/work/:slug',
        name: 'work',
        component: () =>
            import ('../views/Details.vue'),
    },
    {
        path: '/work/:slug',
        name: 'slug',
        component: () =>
            import ('../views/Details.vue'),
    },
    {
        path: '/works',
        name: 'Works',
        component: () =>
            import ('../views/Work.vue'),
    },
    {
        //path: "/:catchAll(.*)",
        path: "/:catchAll(.work/*)",
        name: 'not found',
        component: () =>
            import ('../views/404.vue'),
    },
    {
        //path: "/:catchAll(.*)",
        path: "/:catchAll(.*)",
        name: 'not found',
        component: () =>
            import ('../views/404.vue'),
    },
    {
        path: "/experience",
        name: 'Experiences',
        component: () =>
            import ('../views/Roadmap.vue'),
    },
    {
        path: "/tools",
        name: 'Tools',
        component: () =>
            import ('../views/Tools.vue'),
    },
    {
        path: "/gallery",
        name: 'Gallery',
        component: () =>
            import ('../views/Gallery.vue'),
    },
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router